////
/// Anchors
/// @group base
////


a {
    @include position(relative);
    @include transition(background, color, border-color);
    @include will-change(background, color, border-color);
    background-color: transparent;
    border-bottom-width: $border-width-hairline;
    border-bottom-style: $border-style-default;
    text-decoration: none;
    // remove gaps in links underline in iOS 8+ and Safari 8+.
    -webkit-text-decoration-skip: objects;

    &,
    &:link {
        color: $color-brand;
        border-bottom-color: $color-text-light;
    }

    &:hover,
    &:focus,
    &:active {
        border-bottom-color: $color-brand;
        outline-width: 0;
    }

    &:visited {
        color: $color-brand--hard;

        &:hover,
        &:focus,
        &:active {
            border-bottom-color: $color-brand--hard;
        }
    }

    h2 &,
    h3 & {
        border-bottom-width: 0;

        &:link {
            color: $color-text;
        }

        &:visited {
            color: mix($color-white, $color-text, 10%);
        }
    }
    h3 & {
        &:hover,
        &:focus,
        &:active {
            text-decoration: underline;
        }
    }

    .dark-background & {
        &:link,
        &:visited {
            color: $color-white;
            border-bottom-color: $color-text-alt;

            &:hover,
            &:focus,
            &:active {
                border-bottom-color: $color-white;
            }
        }
    }

    // hide extra content, text targetted only to screen readers
    span:not([class]):not([itemprop]) {
        @include visually-hidden;
    }
}

/**
 * Title Anchors inside Headings
 */
.title-anchor {
    @include transition(border-color);
    @include will-change(color, border-bottom-color);
    display: inline-block;
    border-bottom-width: $border-width-thin;

    &,
    &:link {
        border-bottom-color: transparent;
    }

    &:hover,
    &:focus,
    &:active {
        color: $color-text;
        border-bottom-color: $color-brand;
    }
}

/**
 * Fragment Anchors inside Headings
 */
.fragment-anchor {
    @include case(lowercase, $letter-spacing-default);
    display: none;
    visibility: hidden;
    margin-left: $measure-medium;
    font-family: $font-family-monospace;
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    font-variant-ligatures: none;
    line-height: $line-height-small;
    text-shadow: none;
    vertical-align: middle;

    &,
    &:link {
        color: $color-text-alt;
        border-bottom-width: $border-width-hairline;
    }

    &:visited {
        color: mix($color-black, $color-text-alt, 10%);
    }

    &:hover,
    &:focus,
    &:active {
        border-bottom-color: $color-text-alt;
        // when appearing inside a heading, disable underline
        text-decoration: none;
    }

    &:visited {
        &:hover,
        &:focus,
        &:active {
            border-bottom-color: mix($color-black, $color-text-alt, 10%);
        }
    }

    %heading-elements:hover &,
    %heading-elements:focus &,
    %heading-elements:active &,
    &:target {
        display: inline-block;
        visibility: visible;
    }
}
/**
 * Related Anchors
 */
.related-anchor::after {
    // NO-BREAK SPACE, LEFTWARDS ARROW WITH HOOK
    content: " ↩︎";
}
