////
/// Forms
/// @group base
////


input[type="date"],
input[type="datetime"],
input[type="datetime-local"],
input[type="email"],
input[type="month"],
input[type="number"],
input[type="password"],
input[type="range"],
input[type="search"],
input[type="tel"],
input[type="text"],
input[type="time"],
input[type="week"],
input[type="url"],
optgroup,
select,
textarea {
    @extend %form-elements !optional;
    margin: 0;
    font-size: $font-size-default;
}

input,
textarea {
    @include border-radius(thin);
    @include transition(border-color);
    @include will-change(border-color);
    box-sizing: inherit !important;
    padding: 0.4em;
    border: $border-width-thin $border-style-default $color-text-light;
    outline-style: none;

    &:hover {
        border-color: $color-brand--soft;
    }

    &:focus {
        border-color: $color-brand;
    }

    &[disabled] {
        opacity: opacity("shade--soft");
        pointer-events: none;
    }

    &::-moz-focus-inner {
        padding: 0;
        margin: 0;
    }
}

input {
    overflow: visible;

    &[type="number"]::-webkit-inner-spin-button,
    &[type="number"]::-webkit-outer-spin-button {
        height: auto;
    }

    &[type="search"] {
        // correct odd appearance in Chrome and Safari
        -webkit-appearance: textfield;
        // correct the outline style in Safari
        outline-offset: -2px;

        &::-webkit-search-cancel-button,
        &::-webkit-search-decoration {
            -webkit-appearance: none;
        }
    }

    &::-webkit-file-upload-button {
        // correct inability to style clickable types in iOS and Safari
        -webkit-appearance: button;
    }
}

textarea {
    overflow: auto;
}
